$font-style: 'MoMA sans', Helvetica;

.events {
    display: flex;
    justify-content: center;

}
.events__body {
    width: 1280px;
    height: 950px;
    font-family: $font-style;
    display: flex;
    flex-direction: row;
    margin-top: 200px;
}
.events__title {
    width: 30%;
    font-size: 40px;
    font-weight: 900;
    
}
.events__title_text{   
    position: sticky;
    top: 185px;
    padding-left: 10px;
}
.events__topic_conteiner {
    width: 70%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;

}
.events__topic {
    width: 100%;
    height: 236px;
    display: flex;
    flex-direction: row;
}
.events__topic_info {
    width: 65%;
    height: 236px;
    padding-left: 10%;
}
.events__topic_title {
    font-size: 22px;
    font-weight: 900;
}
.events__topic_time {
    margin-top: 20px;
    font-size: 18px;
    font-weight: 900;
}
.events__topic_subtitle {
    font-size: 18px;
    font-weight: 100;
    
}
.events__topic_img {
    width: 362px;
    height: 236px;
    display: flex;
    object-fit: cover;
}
.events__link {
    font-size: 18px;
    font-weight: 900;
    padding-left: 10%;
}
.fa-solid {
    font-weight: 900;
}
.fa-arrow-right {
    font-weight: 900;
}